import {
    Component,
    Clock,
    Logo,
    LogIn
} from '../components';
import './reset.scss';
import './pizza_list_page.scss';

class PizzaListPage extends Component {
    constructor(props) {
        super(props);
        this.el = props.el || document.createElement('div');
        this.el.classList.add('wrapper');

        // init components
        this.clock = new Clock();
        this.logo = new Logo();
        this.login = new LogIn();
    }



    render() {
        let content = document.createElement('div');
        content.classList.add('content');

        let header = document.createElement('header');
        content.appendChild(header);

        header.appendChild(this.clock.update());
        header.appendChild(this.logo.update());
        header.appendChild(this.login.update());

        return [
            content
        ];

    }
}

export default PizzaListPage;



