export const bindAll = (context, ...names) => {
    names.forEach(name => {
        if (typeof context[name] === 'function') {
            context[name] = context[name].bind(context);
        } else {
            throw Error(
                `Expected function ${name}. Instead received: ${typeof context[name]}`
            );
        }
    });
};



